
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { mapState } from 'vuex'

export default {
components: {
Grid,
GridItem,
XHeader,
Group
},
computed: {
...mapState({
userInfo: state => state.userInfo
})
},
data() {
return {
itemlist: [],
/*itemlist: [{
title: '签到',
path: 'fieldsign',
url: require('../../../assets/index_sign.png')
}, {
title: '日程',
path: 'schedule',
url: require('../../../assets/index_schedule.png')
}
, {
title: '提醒',
path: '',
url: require('../../../assets/index_remind.png')
}, {
title: '任务',
path: 'TaskList',
url: require('../../../assets/index_task.png')
}, {
title: '审批',
path: 'BusinessList',
url: require('../../../assets/index_approval.png')
}
, {
title: '通讯录',
path: 'addresslist',
url: require('../../../assets/txl.png')
}, {
title: '日志',
path: '/loglist',
url: require('../../../assets/rz.png')
}
, {
title: '更多',
path: '',
url: require('../../../assets/more.png')
}
]*/
}
},
mounted() {
if (this.userInfo.admin === 1) {
this.itemlist.push({
title: '签到',
path: 'fieldsign/default',
url: require('../../../assets/index_sign.png')
}





, {
title: '日程',
path: 'schedule',
url: require('../../../assets/index_schedule.png')
}, {
title: '通讯录',
path: 'addresslist',
url: require('../../../assets/txl.png')
}, {
title: '任务',
path: 'project/default',
url: require('../../../assets/index_task.png')
}, {
title: '审批',
path: 'examinemain',
url: require('../../../assets/index_approval.png')
});
} else {
if (this.userInfo.sign) {
this.itemlist.push({
title: '签到',
path: 'fieldsign/default',
url: require('../../../assets/index_sign.png')
});
}






if (this.userInfo.event) {
this.itemlist.push({
title: '日程',
path: 'schedule',
url: require('../../../assets/index_schedule.png')
});
}
if (this.userInfo.address_book) {
this.itemlist.push({
title: '通讯录',
path: 'addresslist',
url: require('../../../assets/txl.png')
});
}

if (this.userInfo.task) {
this.itemlist.push({
title: '任务',
path: 'project/default',
url: require('../../../assets/index_task.png')
});
}

if (this.userInfo.examine) {
this.itemlist.push({
title: '审批',
path: 'examinemain',
url: require('../../../assets/index_approval.png')
});
}


}


this.itemlist.push({
title: '日志',
path: '/loglist',
url: require('../../../assets/rz.png')
});
},
methods: {
clickCRMItem(item) {
this.$router.push({
path: item.path
})
}
}
}




